<template>
  <div class="listWrap">
    <el-row v-for="(item, index) in listData" :key="index" :gutter="60">
      <el-col v-for="(it, idx) in item" :key="idx" :span="8">
        <div class="itemCol">
          <div class="topText">
            <div class="title">{{ it.name }}</div>
            <div class="colorLine"></div>
            <el-row>
              <el-col :span="16">
                <div class="area">{{ it.introduction }}</div>
              </el-col>
            </el-row>
          </div>
          <button class="itemButton" @click="goDetail(it)">查看更多</button>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { defineComponent, computed } from '@vue/composition-api';
import productJson from '@/assets/product/index.json';

export default defineComponent({
  props: {
    active: {
      type: String,
      required: true,
    },
    search: {
      type: String,
      default: '',
    },
  },
  setup(props, { root: { $router } }) {
    const searchData = (item) => {
      const text = `${item.name}$$@@$$${item.introduction}$$@@$$${item.advantage}`;
      return text.indexOf(props.search) > -1;
    };

    const listData = computed(() =>
      productJson.data.reduce((old, now) => {
        if (now.type === props.active) {
          if (searchData(now)) {
            if (old[old.length - 1]?.length < 3) old[old.length - 1].push(now);
            else old.push([now]);
          }
        }
        return old;
      }, [])
    );
    const goDetail = (item) => {
      // window.open(`/product/detail/${item.id}`);
      $router.push({
        name: 'product-detail',
        params: {id: item.id}
      });
    };
    return {
      listData,
      goDetail,
    };
  },
});
</script>

