<template>
  <div class="product-list-view">
    <div class="bannerWrap">
      <div
        class="bgText j-relative j-flex j-justify-center j-items-center j-mb-10"
      >
        <div class="bgEn j-font-semibold">Development lifecycle</div>
        <div class="bgZh j-absolute-center">核心产品</div>
      </div>
      <div class="inputWrap j-flex j-justify-center j-items-center j-h-14">
        <div class="inputCon j-h-full j-w-120">
          <input
            v-model="searchName"
            class="j-w-full j-h-full"
            type="text"
            placeholder="请输入搜索内容"
            @keyup.enter="searchStart"
          />
        </div>
        <button class="inputBut j-h-full j-w-65" @click="searchStart">
          搜索
        </button>
      </div>
      <img class="icon1Position" src="./images/icon1.png" alt="" />
      <img class="icon2Position" src="./images/icon2.png" alt="" />
    </div>
    <div class="tabsWrap j-w-64">
      <el-tabs v-model="activeKey" stretch>
        <el-tab-pane
          v-for="(item, index) in activeEnum"
          :key="index"
          :label="item.label"
          :name="item.key"
        ></el-tab-pane>
      </el-tabs>
    </div>
    <swiper ref="mySwiper" :options="swiperOptions" class="j-overflow-hidden">
      <swiper-slide v-for="(item, index) in activeEnum" :key="index">
        <div>
          <Item :active="item.label" :search="propsSearch" />
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>
<script>
import {
  defineComponent,
  ref,
  reactive,
  watch,
  computed,
} from '@vue/composition-api';
import Item from './components/item.vue';
import { Swiper, SwiperSlide } from 'vue-awesome-swiper';

export default defineComponent({
  components: {
    Item,
    Swiper,
    SwiperSlide,
  },

  setup() {
    const activeEnum = [
      {
        label: '工具',
        key: '0',
      },
      {
        label: '技术产品',
        key: '1',
      },
    ];
    const activeKey = ref('0');
    const activeLabel = computed(
      () => activeEnum.find((item) => item.key === activeKey.value)?.label || ''
    );
    const propsSearch = ref('');
    const searchName = ref('');
    const searchStart = () => {
      propsSearch.value = searchName.value;
    };

    const swiperOptions = reactive({
      simulateTouch: false, // 禁用滑动播放
    });

    let mySwiper = ref(null);

    /**
     * slideTo*/

    watch(
      () => activeKey.value,
      (idx) => {
        mySwiper.value.$swiper.slideTo(idx, 1000, false);
      }
    );

    return {
      activeEnum,
      activeKey,
      activeLabel,
      searchName,
      propsSearch,
      searchStart,
      mySwiper,
      swiperOptions,
    };
  },
});
</script>
